<template>
	<form class="cn"  @submit.prevent="onSubmit">
		<v-card>
         <v-layout row>
				<v-flex xs10>
					<v-card-title primary-title>
						<div class="headline">{{ title }}</div>
					</v-card-title>
				</v-flex>
				<v-flex xs2>
					<v-card-title primary-title>
						<div class="headline">
							<a href="#" @click.prevent="cancel">
								<v-icon>mdi-window-close</v-icon>
							</a>
						</div>
					</v-card-title>
					
				</v-flex>
			</v-layout>
			<v-card-text>
				<v-container grid-list-md>
					<v-layout wrap>
						<v-flex xs12 sm6>
                    <v-menu
                        v-model="pickingDate"
                        :close-on-content-click="false"
                        :nudge-right="40"
                        lazy
                        transition="scale-transition"
                        offset-y
                        full-width
                        min-width="290px"
                        >
                        <template v-slot:activator="{ on }">
                           <v-text-field v-model="model.date" label="Date"
                              prepend-icon="event"
                              readonly
                              v-on="on"
                              v-validate="'required'" 
                              :error-messages="errors.collect('date')"
                              name="date"
                           />
                        </template>
                        <v-date-picker v-model="model.date" @input="pickingDate = false"></v-date-picker>
                     </v-menu>
						</v-flex>
						<v-flex xs12 sm6>
							<v-text-field v-model="model.shares" label="Shares"
								v-validate="'required|decimal'" 
								:error-messages="errors.collect('shares')"
								name="shares"
								required
							/>
						</v-flex>
						<v-flex xs12 sm6>
							<v-text-field v-model="model.price" label="Price"
								v-validate="'required|decimal'" 
								:error-messages="errors.collect('price')"
								name="price"
								required
							/>
						</v-flex>
					</v-layout>
					<ErrorList  />
				</v-container>
			</v-card-text>

			<v-card-actions>
				
				<v-spacer></v-spacer>
				<v-btn @click.prevent="cancel" color="blue darken-1" flat >Cancel</v-btn>
				<v-btn type="submit" @click.prevent="onSubmit" color="primary" flat>Save</v-btn>
			</v-card-actions>
		</v-card>
	</form>	
</template>


<script>

import { mapState } from 'vuex';
import ErrorList from '@/components/ErrorList';

export default {
	name: 'TradeEdit',
	components: {
      ErrorList
   },
	props: {
		model: {
         type: Object,
         default: null
      },
	},
	data () {
		return {
			pickingDate: false
		}
	},
	computed: {
		title(){
			if(this.model && this.model.id) return 'Edit Trade';
			return 'Create Trade';			
		}
   },
	beforeMount(){
		
	},
	methods: {
		cancel(){
			this.$emit('cancel');
		},
		onSubmit() {
         this.$validator.validate().then(valid => {
            if(valid) this.$emit('submit');
         });         
      }
	}
}
</script>
