export default class Asset {
   id = 0;
   name = '';
   chineseName = '';  
   listed = false; //
   market = '';
   code = '';
   

   marketPrice = 0;

   trades = [];

   constructor(model) {
      for (let property in model) {
         this[property] = model[property];
      }
     
   }

   fullname(){
      return `${this.chineseName} ${this.name}`;
   }

   totalShares(){
      let total = 0;
      for(let i = 0; i < this.trades.length; i++){
         total += this.trades[i].shares;
      }
      return total;
   }

   cost(){
      let total = 0;
      for(let i = 0; i < this.trades.length; i++){
         total += this.trades[i].shares * this.trades[i].price;
      }
      return total;
   }


   marketValue(){
      return this.marketPrice * this.totalShares();
   }

   profit(){
     
      let val = this.marketValue() - this.cost();
      return Math.round(val * 10000)/ 10000;
   }
   
   

   
}
