<template>
	<v-data-table :headers="headers" :items="trades" hide-actions>
      <template slot="headerCell" slot-scope="{ header }">
         <span class="subheading font-weight-light text-success text--darken-3 cn">
            {{ header.text }}
         </span>
      </template>
      <template slot="items" slot-scope="props">
         <tr>
            <td>{{ props.item.date }} </td>
            <td>{{ props.item.shares }} </td>
            <td>{{ props.item.price }} </td>
            <td>
               <v-tooltip top  content-class="top">
                  <a href="#" @click.prevent="edit(props.item.id)"  slot="activator" icon>
                     <v-icon small color="success">mdi-pencil</v-icon>
                  </a>
                  <span class="cn">Edit</span>
               </v-tooltip>
               <v-tooltip top  content-class="top">
                  <a class="ml-3" href="#" @click.prevent="remove(props.item)"  slot="activator" icon>
                     <v-icon small color="error">mdi-delete-circle</v-icon>
                  </a>
                  <span class="cn">Remove</span>
               </v-tooltip>
            </td>
         </tr>
         
      </template>
   </v-data-table>	
</template>


<script>

import { mapState } from 'vuex';

export default {
	name: 'TradeList',
	props: {
		trades: {
         type: Array,
         default: null
		}
	},
	data () {
		return {
			headers: [
				{
					sortable: false,
					text: 'Date',
               value: 'date',
               width: '200px'
				},
				{
					sortable: false,
					text: 'Shares',
					value: 'shares',
					width: '200px'
				},
				{
					sortable: false,
					text: 'Price',
					value: 'price',
					width: '100px'
            },
				{
					sortable: false,
					text: '',
					value: '',
					width: '100px'
				}
			]
		}
	},
	computed: {
		
   },
	beforeMount(){
		
	},
	methods: {
		edit(id){
         this.$emit('edit', id);
		},
		remove(item){
         this.$emit('remove', item.id);
		}
	}
}
</script>
