<template>
	<v-container fluid grid-list-xl fill-height v-if="model">
      <v-layout justify-center  align-center>
			<v-flex xs12>
            <form @submit.prevent="onSubmit">
               <v-card>
                  <v-card-text>
                     <v-container grid-list-md>
                        <v-layout wrap>
                           <v-flex xs12 sm6>
                              <v-text-field v-model="model.name" label="Name"
                                 v-validate="'required'" 
                                 :error-messages="errors.collect('name')"
                                 name="name"
                                 required
                              />
                           </v-flex>
                           <v-flex xs12 sm6>
                              <v-text-field v-model="model.chineseName" label="Chinese Name"
                                 name="chineseName"
                              />
                           </v-flex>
                           <v-flex xs12>
                              <v-checkbox
                                 label="Listed"
                                 v-model="model.listed"
                              />
                           </v-flex>
                           <v-flex xs6 sm6>
                              <v-select :items="markets"  label="Market" name="market"
                              v-validate="`${model.listed ? 'required' : ''}`"
                              :error-messages="errors.collect('market')"
                              v-model="model.market"
                              />
                           </v-flex>
                           <v-flex xs6 sm6>
                              <v-text-field v-model="model.code" label="Code"
                                 name="code" v-validate="`${model.listed ? 'required' : ''}`" 
                                 :error-messages="errors.collect('code')"
                              />
                           </v-flex>
                        </v-layout>
                        <ErrorList  />
								<v-layout row>
									<v-card-title primary-title>
										<div class="headline mr-3">Trades</div>
										<v-btn @click.prevent="addTrade" small color="info">
											Add Trade
										</v-btn>
									</v-card-title>
									<v-dialog v-model="tradeEditting" persistent max-width="500px">
										<trade-edit v-if="tradeModel" :model="tradeModel" 
										@submit="submitTrade" @cancel="cancelEditTrade"
										/>
									</v-dialog>
								</v-layout>
								<v-layout wrap>
									<v-flex xs12>
										<trade-list :trades="model.trades" 
										@edit="editTrade" @remove="removeTrade"
										/>
                           </v-flex>
									
								</v-layout>
                     </v-container>
                     
                  </v-card-text>
                  <v-card-actions>
                     <v-spacer></v-spacer>
                     <v-btn @click.prevent="cancel" color="blue darken-1" flat >Cancel</v-btn>
                     <v-btn type="submit" @click.prevent="onSubmit" color="primary" flat>Save</v-btn>
                  </v-card-actions>
               </v-card>
            </form>
			</v-flex>
     </v-layout>
	</v-container>
    
	
</template>

<script>
import { mapState } from 'vuex';
import { CLEAR_ERROR, SET_ERROR } from '@/store/mutations.type';

import { FETCH_ASSETS, CREATE_ASSET, STORE_ASSET,
EDIT_ASSET, UPDATE_ASSET, DELETE_ASSET,
STORE_TRADE, UPDATE_TRADE, DELETE_TRADE
} from '@/store/actions.type';


import TradeList from '@/components/asset/TradeList';
import TradeEdit from '@/components/asset/TradeEdit';
import MaterialCard from '@/components/material/Card';
import ErrorList from '@/components/ErrorList';

import Asset from '@/models/asset';
import Helper from '@/common/helper';
import { constants } from 'fs';

export default {
	name: 'AssetEditView',
	components: {
      ErrorList,
		'material-card' : MaterialCard,
		'trade-list' : TradeList,
		'trade-edit' : TradeEdit
	},
	data () {
		return {
         id: 0,     
         model: null,
			markets:[],
			
			tradeModel: null,
			tradeEditting: false,

			headers: [
				{
					sortable: false,
					text: 'Name',
					value: 'name'
				},
				{
					sortable: false,
					text: 'Number of shares',
					value: 'shares'
				},
				{
					sortable: false,
					text: 'Market Price',
					value: 'marketPrice'
            },
            {
					sortable: false,
					text: 'Market Value',
					value: 'marketValue'
				},
				{
					sortable: false,
					text: '',
					value: '',
					width: '100px'
				}
			],

			
			editting: false,
			deleting: false,
			selected: 0,

			removeTradeIds: [],
			
         
         sinaStockService: null
			
		}
	},
	computed: {
      ...mapState({
			route: state => state.route,
      }),
		canRemove(){
			if(this.editting) return false; 
			return true;
		},
		canCreate(){
			return !this.editting && !this.deleting;
		}
	},
	beforeMount(){
      if(this.route.params && this.route.params.id){
         this.id =  +this.route.params.id;
         this.edit();
      }else {
         this.create();         
      }
	},
	methods: {
      init(){
         if(this.id) this.edit();
         else this.create();
      },
		create(){
			this.$store.commit(CLEAR_ERROR);
			this.$store.dispatch(CREATE_ASSET)
				.then(editForm => {
               this.model = editForm.asset;
               this.markets = editForm.markets;
				})
				.catch(error => {
					Bus.$emit('errors');
				})
			
		},
		edit(){
			this.$store.commit(CLEAR_ERROR);
			this.$store.dispatch(EDIT_ASSET, this.id)
				.then(editForm => {
					this.model = editForm.asset;
               this.markets = editForm.markets;
				})
				.catch(error => {
					Bus.$emit('errors');
				})
		},
      cancel(){
			this.backToIndex();
      },
      backToIndex(){
         this.$router.push({ path: '/assets' });
      },
		remove(item){
			this.selected = item.id;
			this.deleteConfirm.title = `Are You Sure To Remove Asset: ${item.name} ?`;
			this.deleting = true;
		},
		submitDelete(){
			this.$store.commit(CLEAR_ERROR);
			this.$store.dispatch(DELETE_ASSET, this.selected)
				.then(() => {
					this.fetchData();
					this.model = null;  
					this.deleting = false;
				})
				.catch(error => {
					Bus.$emit('errors');
				})
		},
		cancelDelete(){
			this.deleting = false;
		},
      onSubmit(){
         this.$validator.validate().then(valid => {
            if(valid){
               if(this.model.id) this.update();
               else this.store();
            }
         }); 
			
		},
		store(){
         this.$store.dispatch(STORE_ASSET, this.model)
				.then(id => {
               Bus.$emit('success');
               this.backToIndex();
				})
				.catch(error => {
					if(!error)  Bus.$emit('errors');
					else this.$store.commit(SET_ERROR, error);
				})
		},
		update(){
			if(this.removeTradeIds.length){
				this.$store.dispatch(DELETE_TRADE, this.removeTradeIds.join())
				.then(()=> {
               this.submitUpdate();
				})
				.catch(error => {
					if(!error)  Bus.$emit('errors');
					else this.$store.commit(SET_ERROR, error);
				})
			}else{
				this.submitUpdate();
			}
		},
		submitUpdate(){
			this.$store.dispatch(UPDATE_ASSET, this.model)
				.then(()=> {
					Bus.$emit('success');
					this.backToIndex();
				})
				.catch(error => {
					if(!error)  Bus.$emit('errors');
					else this.$store.commit(SET_ERROR, error);
				})
		},
		addTrade(){
			this.tradeModel = {
				id: '',
				date: '',
				shares: '',
				price: ''
			};
			this.tradeEditting = true;
		},
		editTrade(id){
			let trade = this.model.trades.find(item => item.id === id);
			this.tradeModel = { ...trade };
			this.tradeEditting = true;
		},
		removeTrade(id){
			let index = this.model.trades.findIndex(item => item.id === id);
			this.model.trades.splice(index, 1);

			this.removeTradeIds.push(id);
		},
		submitTrade(){
			if(this.tradeModel.id){
				let index = this.model.trades.findIndex(item => item.id === this.tradeModel.id);
				this.model.trades[index].date = this.tradeModel.date;
				this.model.trades[index].price = this.tradeModel.price;
				this.model.trades[index].shares = this.tradeModel.shares;
			}else{
				this.tradeModel.id = 0 - (this.model.trades.length + 1);
				this.model.trades.push({ ...this.tradeModel })
			}

			this.cancelEditTrade();
		},
		cancelEditTrade(){
			this.tradeEditting = false;
			this.tradeModel = null;
			
		}
		
	}
}
</script>



